
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapState,mapGetters,mapMutations } from 'vuex'
export default {
name: 'app',
data:function(){
return{
topics:[
{name:'all',desc:''},
{name:'good',desc:''},
{name:'share',desc:''},
{name:'ask',desc:''},
{name:'job',desc:''},
{name:'test',desc:''}
],











}
},
components: {
Tab,
TabItem,
XHeader,
Actionsheet
},
created:function(){
var loginStatus=window.localStorage.getItem('loginStatus')

if(loginStatus){
this.mutationIsLogined();
this.mutationLoginname(window.localStorage.getItem('loginname'))
this.mutationAccesstoken(window.localStorage.getItem('accesstoken'))
}

},
methods:{
...mapMutations(['mutationIsLogined','mutationLoginname','mutationAccesstoken']),
routeto:function(index){
this.$router.push('/topiclist/'+this.topics[index].name);  
},

ShowMoreMenus:function(){
this.isShowMoreMenus=true;
},
clickMoreMenu:function(menukey,menuitem){
this.$router.push('/'+menukey)
}
},
computed:{







...mapState(['isShowMoreMenus','isLogined','unLoginedMoreMenus','loginedMoreMenus']),
...mapGetters(['moreMenus']),
isShowMoreMenus:{


get:function(){return this.$store.state.isShowMoreMenus},

set:function(nval){this.$store.commit('mutationIsShowMoreMenus',nval)},
},
},
}



